<template>
    <div>
        <br>
        <ul class="list-group">
            <li class="list-group-item clearfix" v-for="item in this_total_each_count" :key="item.id">
                <img :src="this_card_detail[item.id].src" width="75" height="75">
                {{this_card_detail[item.id].name}}
                <h3 class="float-end">
                    <span class="badge bg-danger" v-if="item.id === 0">{{item.count}}</span>
                    <span class="badge bg-warning" v-else-if="item.id === 1 || item.id === 2">{{item.count}}</span>
                    <span class="badge bg-dark" v-else>{{item.count}}</span>
                </h3>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:'Static',
    props:{
        card_detail: Object,
        total_each_count: Array
    },
    data(){
        return{
            this_card_detail:this.card_detail,
            this_total_each_count: this.total_each_count
        }
    },
    watch:{
        card_detail(){
            this.this_card_detail = this.card_detail
        },
        total_each_count(){
            this.this_total_each_count = this.total_each_count 
        }
    }
}
</script>