<template>
    <div>
        <div class="card">
            <div class="card-body">
                <form class="d-grid gap-2 col-6 mx-auto">
                    <div class="mb-3">
                        <label class="form-label">模式</label>
                        <select class="form-select" aria-label="card_count_select" v-model="this_draw_type">
                            <option value="0">一般</option>
                            <option value="1">加倍</option>
                        </select>
                    </div>
                    <div class="mb-3">
                        <label class="form-label">抽卡張數</label>
                        <select class="form-select" aria-label="card_count_select" v-model="this_draw_count">
                            <option value="1">單抽</option>
                            <option value="10">10抽</option>
                            <option value="35">35抽</option>
                        </select>
                    </div>
                    <div class="mb-3">
                        <div class="d-grid gap-2">
                            <button class="btn btn-primary" type="button" @click="reset_card" :disabled="reset_btn_status">{{reset_btn}}</button>
                        </div>
                    </div>
                    <div class="mb-3">
                        <div class="d-grid gap-2">
                            <button class="btn btn-primary" type="button" @click="reset_card_no" :disabled="reset_no_btn_status">{{reset_no_btn}}</button>
                        </div>
                    </div>
                </form>
                <br>
            </div>
        </div> 
    </div>
</template>

<script>
export default {
    name: 'Setting',
    props:{
        draw_type: String,
        draw_count: String
    },
    data(){
        return{
            this_draw_count: this.draw_count,
            this_draw_type: this.draw_type,
            reset_btn:"重置當輪卡匣",
            reset_btn_status:false,
            reset_no_btn:"重置所有數據",
            reset_no_btn_status:false
        }
    },
    watch:{
        this_draw_count: function(){
            this.update_draw_array();
        },
        this_draw_type: function(){
            this.update_draw_array();
        }
    },
    methods:{
        update_draw_array(){
            this.$emit("updated_count_and_type", {'count': this.this_draw_count, 'type': this.this_draw_type});
        },
        reset_card(){
            this.reset_btn_status = true;
            this.reset_btn = "已清除!!"
            this.$emit('reset_card');
            setTimeout(this.recover1, 1000);
        },
        reset_card_no(){
            this.reset_no_btn_status = true;
            this.reset_no_btn = "已清除!!"
            this.$emit('reset_card_no');
            setTimeout(this.recover2, 1000);
        },
        recover1(){
            this.reset_btn = "重置當輪卡匣"
            this.reset_btn_status = false;
        },
        recover2(){
            this.reset_no_btn = "重置所有數據"
            this.reset_no_btn_status = false;
        }
    }
}
</script>

<style></style>