<template>
    <div>
        <img :src="src" :style="return_border(index)" :width="current_width">
    </div>
</template>

<script>
export default {
    name:'Displaycard',
    props:{
        name: String,
        src: String,
        parent_width: Number,
        index: Number
    },
    computed:{
        current_width(){
            if(this.parent_width >= 500){
                return 100;
            }
            return this.parent_width / 6;
        },
        return_border1(index){
            if(index == 0){
                return "margin:0 auto; border:5px solid #04ffb5"
            }else if(index == 1 || index == 2){
                return "margin:0 auto; border:5px solid red"
            }else{
                return "margin:0 auto; border:5px solid white"
            }
        }
    },
    methods:{
        return_border(index){
            if(index == 0){
                return "margin:0 auto; border:4px red solid"
            }else if(index == 1 || index == 2){
                return "margin:0 auto; border:4px gold solid"
            }else{
                return "margin:0 auto; border:4px white solid"
            }
        }
    }
}
</script>